import {translationsRO} from './ro';
import {translationsEN} from './en';

const languages = [
    {code: 'en', name: 'English'},
    {code: 'ro', name: 'Română'}
]

const translations = Object.assign({}, translationsRO, translationsEN);

export const getTranslation = (lang, text1, text2) => {
    return translations[lang ? lang : 'en'][text1][text2];
}
