import React from 'react';
import logo from '../../images/logo1.png';
import {Container} from 'react-bootstrap';
import {HashLink as Link} from 'react-router-hash-link';
import {getTranslation} from '../../text/translate';
import './NavBar.css';

export class NavBarContents extends React.Component {
    constructor(props) {
        super(props);
        this.state = {hidden: 'hidden'};
    }

    notHidden() {
        return this.state.hidden === '' ? 'hidden' : '';
    }

    render() {
        let toggle = () => {
            this.setState(() => ({hidden: this.notHidden()}));
        }

        return (
            <div id={'NavBar'} className={this.state.hidden}>
                <Link to={'/#home'} className={'logo ' + this.state.hidden}>
                    <img src={logo} alt={'logo'} id={'logo'}/>
                </Link>

                <p id={'mobile-menu-hamburger'} onClick={toggle}>
                    <i className={'fa fa-bars'}/>
                </p>

                <div className={'menuItems ' + this.state.hidden}>
                    <Link to={'/#home'} id={'homeItem'} className={'menuItem ' + this.state.hidden} onClick={toggle}>
                        {getTranslation(this.state.language, 'pages', 'home')}
                    </Link>

                    {/*<Link to={'/#about-us'} className={'menuItem'} onClick={toggle}>*/}
                    {/*    {getTranslation(this.state.language, 'pages', 'about')}*/}
                    {/*</Link>*/}

                    <Link to={'/#workshops'} className={'menuItem'} onClick={toggle}>
                        {getTranslation(this.state.language, 'pages', 'workshops')}
                    </Link>

                    <Link to={'/#conferences'} className={'menuItem'} onClick={toggle}>
                        {getTranslation(this.state.language, 'pages', 'conferences')}
                    </Link>

                    <Link to={'/#hackathon'} className={'menuItem'} onClick={toggle}>
                        {getTranslation(this.state.language, 'pages', 'hackathon')}
                    </Link>

                    <Link to={'/#schedule'} className={'menuItem'} onClick={toggle}>
                        {getTranslation(this.state.language, 'pages', 'schedule')}
                    </Link>

                    <Link to={'/#winners'} className={'menuItem'} onClick={toggle}>
                        {getTranslation(this.state.language, 'pages', 'winners')}
                    </Link>

                    <Link to={'/#our-team'} className={'menuItem'} onClick={toggle}>
                        {getTranslation(this.state.language, 'pages', 'team')}
                    </Link>

                    <Link to={'/#partners'} className={'menuItem'} onClick={toggle}>
                        {getTranslation(this.state.language, 'pages', 'partners')}
                    </Link>

                    <Link to={'/#contact'} className={'menuItem'} onClick={toggle}>
                        {getTranslation(this.state.language, 'pages', 'contact')}
                    </Link>
                </div>
            </div>
        )
    }
}

class NavBar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {'show': false};
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll, true);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    handleScroll = e => {
        let th = 500;
        let scroll = window.scrollY;

        if (scroll > th && this.state.show === false) {
            this.setState({
                'show': true
            });
        }

        if (scroll < th && this.state.show === true) {
            this.setState({
                'show': false
            });
        }
    }

    render() {
        if (this.state.show === false) {
            return null;
        }

        return <Container id={'floating-menu'} className={'fade-in'} fluid={true}>
            <NavBarContents language={this.state.language}/>
        </Container>
    }
}

export default NavBar;
