import React from 'react';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';
import NavBar from './pages/NavBar/NavBar';
import Home from './pages/Home/Home.js';
import AboutUs from './pages/AboutUs/AboutUs';
import Workshops from './pages/Workshops/Workshops';
import Conferences from './pages/Conferences/Conferences';
import Hackathon from './pages/Hackathon/Hackathon';
import Schedule from './pages/Schedule/Schedule';
import Winners from './pages/Winners/Winners';
import OurTeam from './pages/OurTeam/OurTeam';
import Partners from './pages/Partners/Partners';
import Footer from './pages/Footer/Footer';
import './App.css';
import './Media.css';

import '@fontsource/lexend-mega';
import '@fontsource/nunito-sans';

class App extends React.Component {
    state = {
        language: 'en'
    };

    changeLanguageHandler = (language) => {
        this.setState({language: language});
    }

    render() {
        return (
            <Router id={'App'}>
                <Switch>
                    <Route path={'/'}>
                        <NavBar language={this.state.language}/>
                        <Home language={this.state.language}/>
                        {/*<AboutUs language={this.state.language}/>*/}
                        <Workshops language={this.state.language}/>
                        <Conferences language={this.state.language}/>
                        <Hackathon language={this.state.language}/>
                        <Schedule language={this.state.language}/>
                        <Winners language={this.state.language}/>
                        <OurTeam language={this.state.language}/>
                        <Partners language={this.state.language}/>
                        <Footer/>
                    </Route>
                </Switch>
            </Router>
        )
    }
}

export default App;
