import React from 'react';
import {getTranslation} from '../../text/translate';
import './Workshops.css';

const Card = (props) => {
    const card = props.card;

    return (
        <div className={'card'}>
            <div className={'card-title-section'}>
                <h4 className={'time'}>{card.date}, {card.time}</h4>
                <h3 className={'card-title'}>{card.title}</h3>
                <h4 className={'card-speakers'}>{card.speakers}</h4>
                <hr/>
                <p className={'card-description'}>{card.description}</p>
            </div>

            <a href={card.link} className={'card-link'}>{card.link}</a>
        </div>
    )
}

const ListItem = (props) => {
    return (
        <li>{props.value}</li>
    )
}

const Workshops = (props) => {
    return (
        <div id={'workshops'} className={'page'}>
            <h4 className={'date'}>{getTranslation(props.language, 'workshops', 'date')}</h4>
            <h2 className={'title'}>{getTranslation(props.language, 'workshops', 'title')}</h2>
            <ul className={'details'}>
                {getTranslation(props.language, 'workshops', 'details').map((item, index) =>
                    <ListItem key={'detail' + index} value={item}/>
                )}

                <p>{getTranslation(props.language, 'workshops', 'github')}
                    <a target={'_blank'} href={'https://github.com/Nitro-Language-Processing/Workshops'}>
                        https://github.com/Nitro-Language-Processing/Workshops
                    </a>
                </p>
            </ul>

            <ul className={'cards'}>
                {getTranslation(props.language, 'workshops', 'cards').map((item, index) =>
                    <Card key={'card' + index} card={item}/>
                )}
            </ul>
        </div>
    )
}

export default Workshops;
