const TeamMember = (props) => {
    return (
        <div className={'teamMember'}>
            <img src={props.image} alt={props.name} className={'memberImage'}/>
            <h4 className={'memberName'}>{props.name}</h4>
            {/*<p className={'memberDescription'}>{props.description}</p>*/}
            <a href={'https://www.facebook.com' + props.contact} target={'_blank'}>
                <p className={'contactLink'}>{'fb: ' + props.contact}</p>
            </a>
        </div>
    )
}

