import React from 'react';
import {getTranslation} from '../../text/translate';
import './Partners.css';

import logoAsmi from '../../images/sponsors/logo-asmi.png';
import logoBitDefender from '../../images/sponsors/logo-bitdefender.png';
import logoEeml from '../../images/sponsors/logo-eeml-black.png';
import logoSparktech from '../../images/sponsors/logo-sparktech.png';

import logoOlr from '../../images/sponsors/logo-olr.png';
import logoSigmoid from '../../images/sponsors/logo-sigmoid-black.png';

const Sponsor = (props) => {
    return (
        <a className={'sponsor'} target={'_blank'} href={props.link}>
            <img src={props.image} alt={props.name} className={props.imageClass}/>
        </a>
    )
}

const Partners = (props) => {
    return (
        <div id={'partners'} className={'page'}>
            <h2 className={'title'}>{getTranslation(props.language, 'partners', 'title')}</h2>

            <div className={'sponsorList'}>
                <Sponsor image={logoBitDefender}
                         link={'https://www.bitdefender.ro/'}
                         imageClass={'sponsorImage'}/>
                <Sponsor image={logoEeml}
                         link={'https://www.eeml.eu/'}
                         imageClass={'sponsorImage'}/>
                <Sponsor image={logoSparktech}
                         link={'https://www.sparktech.ro/'}
                         imageClass={'sponsorImage'}/>
            </div>

            <div className={'partnerList'}>
                <Sponsor image={logoAsmi}
                         link={'https://www.asmi.ro/'}
                         imageClass={'partnerImage'}/>
                <Sponsor image={logoOlr}
                         link={'https://www.facebook.com/olimpiadadelingvistica/'}
                         imageClass={'partnerImage'}/>
                <Sponsor image={logoSigmoid}
                         link={'https://www.linkedin.com/company/sigmoid/?originalSubdomain=md'}
                         imageClass={'partnerImage'}/>
            </div>

            <h4>{getTranslation(props.language, 'partners', 'details')}</h4>
            <div className={'button'}>
                <a className={'button-link'}
                   target={'_blank'}
                   href={'mailto:contact@nitronlp.rocks'}>
                    {getTranslation(props.language, 'partners', 'button')}
                </a>
            </div>
        </div>
    )
}

export default Partners;
