import React from 'react';
import {FaFacebook, FaInstagram, FaLinkedin} from 'react-icons/fa';
import {HiOutlineMail} from 'react-icons/hi';
import './Footer.css';

import logoUB from '../../images/logo-ub.png';

const Contact = (props) => {
    return (
        <a href={props.link} target={'_blank'} className={'contactSlot'}>
            <props.icon className={'icon'}/>
        </a>
    )
}

const Footer = () => {
    return (
        <div id={'contact'}>
            <div className={'contactLinks'}>
                <div className={'socialMedia'}>
                    <Contact icon={FaFacebook} link={'https://www.facebook.com/nitro.nlp'}/>
                    <Contact icon={FaInstagram} link={'https://www.instagram.com/nitro_nlp/'}/>
                    <Contact icon={FaLinkedin} link={'https://www.linkedin.com/events/nitrolanguageprocessinghackatho6901184778507558913/'}/>
                </div>

                <a href={'mailto:contact@nitronlp.rocks'} target={'_blank'} className={'contactMail'}>
                    <HiOutlineMail className={'icon'}/>
                    <p className={'contactLink'}>contact@nitronlp.rocks</p>
                </a>
            </div>

            <div className={'footerLogo'}>
                <a href={'https://unibuc.ro/'} target={'_blank'}>
                    <img src={logoUB} alt={'Universitatea din Bucuresti'} className={'logoUB'}/>
                </a>
            </div>
        </div>
    )
}

export default Footer;
