import React from 'react';
import {VerticalTimeline, VerticalTimelineElement} from 'react-vertical-timeline-component';
import {getTranslation} from '../../text/translate';
import {FaPeopleCarry} from 'react-icons/fa';
import {GiCoffeeCup, GiPlasticDuck, GiTeamIdea, GiTrophyCup} from 'react-icons/gi';
import {GoTools} from 'react-icons/go';
import {RiFilePaper2Fill} from 'react-icons/ri';
import 'react-vertical-timeline-component/style.min.css';
import './Schedule.css';

const Schedule = (props) => {
    const style1 = {background: 'var(--schedule-color-1)', color: 'var(--light2)'}
    const iconStyle1 = {borderRight: '7px solid var(--schedule-color-1)'}
    const icons = [<GiCoffeeCup/>, <GiPlasticDuck/>, <FaPeopleCarry/>, <GoTools/>, <RiFilePaper2Fill/>, <GiTeamIdea/>,
        <GiTrophyCup/>]

    return (
        <div id={'schedule'} className={'page'}>
            <h2 className={'title'}>{getTranslation(props.language, 'schedule', 'title')}</h2>

            <VerticalTimeline className={'vertical-timeline'}>
                {getTranslation(props.language, 'schedule', 'dates').map((date, index) =>
                    <VerticalTimelineElement
                        key={index}
                        className={'vertical-timeline-element'}
                        contentStyle={style1}
                        contentArrowStyle={iconStyle1}
                        dateClassName={'date'}
                        iconClassName={'scheduleIcon'}
                        date={date}
                        icon={icons[index]}
                    >
                        <h3 className={'vertical-timeline-element-title'}>
                            {getTranslation(props.language, 'schedule', 'events')[index]}
                        </h3>
                    </VerticalTimelineElement>
                )}
            </VerticalTimeline>
        </div>
    )
}

export default Schedule;
