import React from 'react';
import {getTranslation} from '../../text/translate';
import './Conferences.css';

const ListItem = (props) => {
    return (
        <li>{props.value}</li>
    )
}

const Conferences = (props) => {
    return (
        <div id={'conferences'} className={'page'}>
            <h4 className={'date'}>{getTranslation(props.language, 'conferences', 'date')}</h4>
            <h2 className={'title'}>{getTranslation(props.language, 'conferences', 'title')}</h2>
            <ul className={'details'}>
                {getTranslation(props.language, 'conferences', 'details').map((item, index) =>
                    <ListItem key={index} value={item}/>
                )}
            </ul>

            <div className={'button'}>
                <a className={'button-link'}
                   target={'_blank'}
                   href={'https://docs.google.com/forms/d/e/1FAIpQLSezz-ilOabT1k12VGXAu6BWqWjzW06ftw2scwZLw2tylOrVOA/viewform?usp=sf_link'}>
                    {getTranslation(props.language, 'conferences', 'button')}
                </a>
            </div>
        </div>
    )
}

export default Conferences;
